@include tablet {
    .seccion-inferior{
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.entrada-blog{
    margin-bottom: 2rem;
    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    &:last-of-type{
        margin-bottom: 0;
    }
    
}

.texto-entrada {
    h4 {
        font-size: 2.5rem;
        margin: 0;
        line-height: 1.4rem;
        margin-top: 2rem;
        @include tablet {
           margin-top : 1rem;
        }
        &::after {
            content: "";
            display: block;
            width: 15rem;
            height: 0.5rem;
            background-color: $marron;
            margin-top: 1.5rem;
        }
    }
    a {
        color: $negro;
    }
    span {
        color: $verde;
    }
}