
/* --------- GALERIA 1 ---------*/

.galeria-imagenes{
    @include resetear-lista;

    @include telefono {
        @include grid(2, 2rem);
    }

    @include tablet {
        @include grid(3, 2rem);
    }
    @include desktop {
        @include grid(6,2rem);
    }
}


.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;

    background-color: rgb(0 0 0 / .5);
    picture {
        display: block;
        max-width: 80rem;
    }
}

.btn-cerrar {
    width: 5rem ;
    height: 5rem;
    background-color: $blanco;
    border-radius: 50%;
    display: grid;
    place-items: center;
    font-weight: 700;

    &:hover {
        cursor: pointer;
    }
}



/* --------- GALERIA 2 ---------*/

.galeria-imagenes2 {
    display: flex;
    justify-content: space-between;
    margin-top: 5rem;
}