


html{
    font-size: 62.5%;  // 1 rem = 10px
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit; // El padding no representa un problema
}
body{
    font-family: $fuente_principal;
    font-size: 1.6rem;

    &.fijar-body {
        overflow: hidden;
    }
}
p{
    font-size: 2rem;
}
.contenedor{
    width:95%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img,
picture{
    width: 100%;
    display: block;
}

h1, h2, h3 {
    margin: 0 0 5rem 0;
}
h1{
    font-size: 4rem;
    font-family: $fuente_principal;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
.negrita {
    font-weight: $bold;
}