/** Media Queries **/
@mixin telefono {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}

@mixin grid($columnas, $espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton($bgcolor, $display, $color_letras:$blanco) {
    background-color: $bgcolor;
    color: $color_letras;
    font-weight: $bold;
    text-decoration: none;
    padding: 1rem 3rem;
    text-align: center;
    display: $display;
    margin-top: calc($separacion / 2);

    &:hover{
        background-color: darken($bgcolor, 5);
    }
}

// , $display, $fontColor:$blanco  --> per posar un default

@mixin resetear-lista {
    margin: 0;
    padding: 0;
    list-style: none;
}