.no-webp .inicio {
    background-image: url(../img/header.jpg);
}
.webp .inicio {
    background-image: url(../img/header.webp);
}

.header {
    background-color: $negro;
    padding: 1rem 0 3rem 0;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    &.inicio {
        @include tablet{
            min-height: 70rem;
        }
        .contenido-header {
            @include tablet {
                min-height: 70rem;
            }
        }
    }
}

.contenido-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    h1{
        color: $blanco;
        text-align: left;
    }
}

.logos {
    display: flex;
    align-content: center;
    .logo-lletres{
        width: 35rem;
        height: 10rem;
    }
    .logo-pantera{
        max-width: 10rem;
        max-height: 10rem;
    }

    @include tablet {
        display: flex;
        align-items: center;
        .logo-lletres {
            max-width: 35rem;
            max-height: 15rem;
        }
        .logo-pantera {
            max-width: 15rem;
            max-height: 15rem;
        }
    }
}

.titulo-pagina{
    text-align: center;
}
