

.icono-template {
    text-align: center;
    h2 {
        text-transform: uppercase;
        padding-top: 2rem;
        margin-bottom: 3rem;
        .h2-inhale{
            max-width: 40rem;
        }
        .h2-bien{
            margin-top: 2rem;
            margin-bottom: 5rem;
        }
    }
    p {
        align-content: center;
    }
    
  
}

@include tablet {
    .iconos-nosotros{
        @include grid(3, 3rem);
        
    }
}

