.formulario{
    display:flex;
    flex-direction: column;
    row-gap: 2rem;
    margin-bottom: 2rem;
}
.informacion-form{
    .campo {
        display:flex;
        flex-direction: column;
        margin-bottom: 2rem;
        .campo__label {
            text-transform: uppercase;
            font-weight: $bold;
        }
        .campo__field {
            border-radius: 1rem;
            height: 4rem;
            border-color: $gris;
            border-style: solid;
        }
    }
    legend {
        font-size: 2rem;
    }
    p{
        font-size: 1.4rem;
        margin:0;
    }
}

.boton-enviarform{
    border-style: none;
}
