

.barra {
    display: flex;
    flex-direction: column;
    padding-top: 3rem;
    align-items: center;
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
    }
}

.navegacion {
    @include tablet {
        display: flex;
        text-align: center;
    }

    a{
        color: $blanco;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        @include tablet {
            margin-right: 2rem;
            &:last-of-type{
                margin-right: 0;
            }
        }
        
        &:hover{
            color: $verde;
            cursor: pointer;
        }
    }
}