.testimoniales {
    background-color: $verde;
    padding: 2rem;
    border-radius: 3rem;
    
    blockquote {
        padding-left: 5rem;
        position: relative;
        color: $blanco;
        &::before {
            content: '';
            background-image: url(../img/comillas.svg);
            background-repeat: no-repeat;
            width: 6rem;
            height: 8rem;
            display: inline-block;
            position: absolute;
            left: -3rem;
        }
      
    }
    p {
        text-align: right;
        color: $blanco;
    }
}