.contenido-nosotros {
    @include tablet {
        @include grid (2, 2rem);
        
    }
}

.texto-nosotros blockquote {
    font-weight: $black;
    font-size: 2.6rem;
    margin: 0;
    padding-left: 1rem 0 3rem 0;
}

.mas-nosotros h2 {
    text-align: center;
    margin: 4rem auto 2rem auto;
}
.nosotros-club {
    background-color: $gris-oscuro;
    h2 {
        color: $blanco;
        text-align: center;
        padding-top: 5rem;
        margin-bottom: 0;
    }
}
.nosotros-lista {
    padding-right: 4rem;
   li {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10rem;
   
   }
   p ,h3{
    color: $blanco;
    
}
   h3 {
    text-transform: uppercase;
    font-size: 6rem;
    font-weight: $black;
   }
   p {
    font-size: 3rem;
    text-align: center;
    font-weight: $regular;
    max-width: 85rem;
    
   }
   
    picture {
        max-width: 100rem;
    }
    
}

li:nth-child(2) {
    p {
        max-width: 150rem;
    }
} 