.contenedor-anuncios{
    @include grid(3, 2rem);
}


.coctel{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: lighten($gris, 5);
    border: 1px solid lighten($gris, 9);
    .coctel-contenido{
        display: flex;
        flex-direction: column;
       
        padding: 2rem;
        h3 , p {
            margin: 0;
            text-align: center;
        }
        .precio, .descripcion{
            text-align: left;
            
        }
        .precio{
            font-size: 3rem;
            color: $verde;
            margin-top: auto;
            font-weight: $bold;
        }
        .descripcion{
            line-height: 2.5rem;
        }
    }
    
}


