/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Finlandica", "IBM Plex Sans", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
  font-family: "Finlandica", "IBM Plex Sans", sans-serif;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.negrita {
  font-weight: 700;
}

.boton-marron {
  background-color: #6a3722;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  margin-top: 2.5rem;
}
.boton-marron:hover {
  background-color: #572d1c;
}

.boton-marron-block {
  background-color: #6a3722;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  margin-top: 2.5rem;
}
.boton-marron-block:hover {
  background-color: #572d1c;
}

.boton-dorado {
  background-color: #503814;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  margin-top: 2.5rem;
}
.boton-dorado:hover {
  background-color: #3c2a0f;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

/* UI */
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #000000;
  padding: 1rem 0 3rem 0;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #FFFFFF;
  text-align: left;
}

.logos {
  display: flex;
  align-content: center;
}
.logos .logo-lletres {
  width: 35rem;
  height: 10rem;
}
.logos .logo-pantera {
  max-width: 10rem;
  max-height: 10rem;
}
@media (min-width: 768px) {
  .logos {
    display: flex;
    align-items: center;
  }
  .logos .logo-lletres {
    max-width: 35rem;
    max-height: 15rem;
  }
  .logos .logo-pantera {
    max-width: 15rem;
    max-height: 15rem;
  }
}

.titulo-pagina {
  text-align: center;
}

.footer {
  padding: 2.5rem 0;
  background-color: #000000;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-bottom: 2.5rem;
}
.footer .copyright {
  text-align: center;
  color: #FFFFFF;
}

.icono-template {
  text-align: center;
}
.icono-template h2 {
  text-transform: uppercase;
  padding-top: 2rem;
  margin-bottom: 3rem;
}
.icono-template h2 .h2-inhale {
  max-width: 40rem;
}
.icono-template h2 .h2-bien {
  margin-top: 2rem;
  margin-bottom: 5rem;
}
.icono-template p {
  align-content: center;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    text-align: center;
  }
}
.navegacion a {
  color: #FFFFFF;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #438c4c;
  cursor: pointer;
}

.contenedor-anuncios {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
}

.coctel {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f9f9f9;
  border: 1px solid white;
}
.coctel .coctel-contenido {
  display: flex;
  flex-direction: column;
  padding: 2rem;
}
.coctel .coctel-contenido h3, .coctel .coctel-contenido p {
  margin: 0;
  text-align: center;
}
.coctel .coctel-contenido .precio, .coctel .coctel-contenido .descripcion {
  text-align: left;
}
.coctel .coctel-contenido .precio {
  font-size: 3rem;
  color: #438c4c;
  margin-top: auto;
  font-weight: 700;
}
.coctel .coctel-contenido .descripcion {
  line-height: 2.5rem;
}

.no-webp .imagen-contacto {
  background-image: url(../img/contacta.jpg);
}

.webp .imagen-contacto {
  background-image: url(../img/contacta.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.texto-entrada h4 {
  font-size: 2.5rem;
  margin: 0;
  line-height: 1.4rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .texto-entrada h4 {
    margin-top: 1rem;
  }
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #6a3722;
  margin-top: 1.5rem;
}
.texto-entrada a {
  color: #000000;
}
.texto-entrada span {
  color: #438c4c;
}

.testimoniales {
  background-color: #438c4c;
  padding: 2rem;
  border-radius: 3rem;
}
.testimoniales blockquote {
  padding-left: 5rem;
  position: relative;
  color: #FFFFFF;
}
.testimoniales blockquote::before {
  content: "";
  background-image: url(../img/comillas.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: inline-block;
  position: absolute;
  left: -3rem;
}
.testimoniales p {
  text-align: right;
  color: #FFFFFF;
}

/* --------- GALERIA 1 ---------*/
.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .galeria-imagenes {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(6, 1fr);
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  background-color: #FFFFFF;
  border-radius: 50%;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.btn-cerrar:hover {
  cursor: pointer;
}

/* --------- GALERIA 2 ---------*/
.galeria-imagenes2 {
  display: flex;
  justify-content: space-between;
  margin-top: 5rem;
}

.formulario {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
  margin-bottom: 2rem;
}

.informacion-form .campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.informacion-form .campo .campo__label {
  text-transform: uppercase;
  font-weight: 700;
}
.informacion-form .campo .campo__field {
  border-radius: 1rem;
  height: 4rem;
  border-color: #ececec;
  border-style: solid;
}
.informacion-form legend {
  font-size: 2rem;
}
.informacion-form p {
  font-size: 1.4rem;
  margin: 0;
}

.boton-enviarform {
  border-style: none;
}

/* Páginas Internas */
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.6rem;
  margin: 0;
  padding-left: 1rem 0 3rem 0;
}

.mas-nosotros h2 {
  text-align: center;
  margin: 4rem auto 2rem auto;
}

.nosotros-club {
  background-color: #141414;
}
.nosotros-club h2 {
  color: #FFFFFF;
  text-align: center;
  padding-top: 5rem;
  margin-bottom: 0;
}

.nosotros-lista {
  padding-right: 4rem;
}
.nosotros-lista li {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10rem;
}
.nosotros-lista p, .nosotros-lista h3 {
  color: #FFFFFF;
}
.nosotros-lista h3 {
  text-transform: uppercase;
  font-size: 6rem;
  font-weight: 900;
}
.nosotros-lista p {
  font-size: 3rem;
  text-align: center;
  font-weight: 400;
  max-width: 85rem;
}
.nosotros-lista picture {
  max-width: 100rem;
}

li:nth-child(2) p {
  max-width: 150rem;
}